import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    // {
    //   path: '/about',
    //   name: 'about',
    //   // route level code-splitting 路由级代码拆分
    //   // this generates a separate chunk (about.[hash].js) for this route  这将为此路由生成单独的块（关于[hash].js）。
    //   // which is lazy-loaded when the route is visited. 在访问路由时延迟加载。
    //   component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    // },
    {
      path: '/test',
      name: 'test',
      component: () => import('./views/test.vue')
    },

    {
      path: '/info',
      name: 'info',
      component: () => import('./views/info.vue')
    },
    {
      path: '/map',
      name: 'map',
      component: () => import('./components/map.vue')
    },
    {
      path: '/location',
      name: 'location',
      component: () => import('./views/location.vue')
    },
    {
      path: '/demo',
      name: 'demo',
      component: () => import('./views/demo.vue')
    },
    {
      path: '/vuex',
      name: 'vuex',
      component: () => import('./views/vuex.vue')
    },
    {
      path: '/parameter',
      name: 'parameter',
      component: () => import('./views/parameter.vue')
    },
    {
      path: '/input',
      name: 'input',
      component: () => import('./views/input.vue')
    },
    {
      path: '/colorChange',
      name: 'colorChange',
      component: () => import('./views/colorChange.vue')
    },
    {
      path: '/flex',
      name: 'flex',
      component: () => import('./views/flex.vue')
    },
    {
      path: '/download',
      name: 'download',
      component: () => import('./views/downloads.vue')
    }
  ]
})
